<template>
  <div class="recommend" ref="recommend">
    <!--  data  -->
    <Scroll ref="scroll" class="recommend-content" :data="discList">
      <div>
        <div v-if="recommends.length" class="slider-wrapper" ref="sliderWrapper">
          <!-- element UI  -->
          <el-carousel indicator-position="outside" height="150px">
            <el-carousel-item v-for="item in recommends" :key="item.id">
                <a :href="item.linkUrl">
                    <img @load="loadImage" :src="item.picUrl" alt="">
                </a>
            </el-carousel-item>
          </el-carousel>
        </div>
        <div class="recommend-list">
          <h1 class="list-title"></h1>
          <ul>
            <li @click="selectItem(item)" v-for="item in discList" :key="item.content_id" class="item">
              <div class="icon">
                <!-- v-lazy -->
                <img v-lazy="item.cover" alt="" width="60" height="60">
              </div>
              <div class="text">
                <h2 class="name">{{item.title}}</h2>
                <p class="desc">{{item.rcmdcontent}}</p>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <!-- loading -->
      <div class="loading-content" v-show="!discList.length"> <!--  -->
        <Loading></Loading>
      </div>
    </Scroll>
    <!--  -->
    <router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
// 
import { getRecommend, getDiscList } from '@/api/recommend';
// 
import Scroll from '@/base/scroll/scroll';
// loading
import Loading from '@/base/loading/loading';
import { ERR_OK } from '@/api/config';
import {playlistMixin} from '@/common/js/mixin' // 
import {mapMutations} from 'vuex'

export default {
  mixins: [playlistMixin],
  data () {
    return {
      recommends: [],
      discList: []
    }
  },
  //  => 
  created () {
    this._getRecommend();
    this._getDiscList();
  },
  // 
  methods: {
    // 
    selectItem (item) {
      this.$router.push({
        path: `/recommend/${item.content_id}`
      })
      // Vuex
      this.setDisc(item)
    },
    handlePlaylist (playlist) {
      const bottom = playlist.length > 0 ? '60px' : ''
      this.$refs.recommend.style.bottom = bottom // minibottom
      this.$refs.scroll.refresh() // scroll
    },
    // 
    _getRecommend () {
      getRecommend().then((res) => {
        if (res.code === ERR_OK) {
          // data
          this.recommends = res.data.slider;
        }
      })
    },
    // 
    _getDiscList () {
      getDiscList().then((res) => {
        if (res.code === ERR_OK) {
          this.discList = res.recomPlaylist.data.v_hot
        }
      })
    },
    // 
    loadImage () {
      if (!this.checkLoaded) {
        this.$refs.scroll.refresh()
        this.checkLoaded = true; //  loadImage
      }
    },
    ...mapMutations({
      setDisc: 'SET_DISC'
    })
  },
  // 
  components: {
    Scroll,
    Loading
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .recommend
    position: fixed
    width: 100%
    top: 88px
    bottom: 0
    .recommend-content
      height: 100%
      overflow: hidden
      .slider-wrapper
        position: relative
        width: 100%
        overflow: hidden
        img
         width : 100%
      .recommend-list
        .list-title
          height: 65px
          line-height: 65px
          text-align: center
          font-size: $font-size-medium
          color: $color-theme
        .item
          display: flex
          box-sizing: border-box
          align-items: center
          padding: 0 20px 20px 20px
          .icon
            flex: 0 0 60px
            width: 60px
            padding-right: 20px
          .text
            display: flex
            flex-direction: column
            justify-content: center
            flex: 1
            line-height: 20px
            overflow: hidden
            font-size: $font-size-medium
            .name
              margin-bottom: 10px
              color: $color-text
            .desc
              color: $color-text-d
      .loading-container
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>



