<template>
  <div class="search">
    <div class="search-box-wrapper">
      <search-box ref="searchBox" @query="onQueryChange"></search-box>
    </div>
    <div ref="shortcutWrapper" class="shortcut-wrapper" v-show="!query">
      <scroll :refreshDelay="refreshDelay" class="shortcut" ref="shortcut" :data="shortcut">
        <div>
          <!--  -->
          <div class="hot-key">
            <h1 class="title"></h1>
            <ul>
              <li @click="addQuery(item.k)" v-for="(item, index) in hotKey" :key="index" class="item"><span>{{item.k}}</span></li>
            </ul>
          </div>
          <!--  -->
          <div class="search-history">
            <h1 class="title">
              <span class="text"></span>
              <span class="clear" @click="showConfirm">
                <i class="icon-clear"></i>
              </span>
            </h1>
            <!--  -->
            <search-list
                  :searches="searchHistory"
                  @selectItem="addQuery"
                  @deleteItem="deleteSearchHistory"
            ></search-list>
          </div>
        </div>
      </scroll>
    </div>
    <!--  -->
    <div ref="searchResult" class="search-result" v-show="query">
      <suggest ref="suggest" @listScroll="blurInput" @selectSearch="saveSearch" :query="query"></suggest>
    </div>
    <!--  -->
    <confirm ref="confirm" text="" confirmBtnText="" @confirm="clearSearchHistory"></confirm>
    <router-view></router-view>
  </div>
</template>

<script>
import SearchBox from '@/base/search-box/search-box' // 
import SearchList from '@/base/search-list/search-list' // 
import Confirm from '@/base/confirm/confirm' // 
import Suggest from '@/components/suggest/suggest' // 
import Scroll from '@/base/scroll/scroll'
import { getHotKey } from '@/api/search' // 
import { ERR_OK } from '@/api/config' // 
import { mapActions, mapGetters } from 'vuex';
import {playlistMixin, searchMixin} from '@/common/js/mixin' // 

export default {
  mixins: [playlistMixin, searchMixin],
  data () {
    return {
      hotKey: [], // 
      refreshDelay: 100
    }
  },
  created () {
    this._getHotKey()
  },
  computed: {
    //  scroll
    shortcut () {
      return this.hotKey.concat(this.searchHistory)
    },
    // 
    ...mapGetters([
      'searchHistory'
    ])
  },
  methods: {
    handlePlaylist (playlist) {
      const bottom = playlist.length > 0 ? '60px' : 0
      // 
      this.$refs.shortcutWrapper.style.bottom = bottom
      this.$refs.searchResult.style.bottom = bottom
      // 
      this.$refs.shortcut.refresh()
      // 
      this.$refs.suggest.refresh()
    },
    // 
    _getHotKey () {
      getHotKey().then(res => {
        if (res.code === ERR_OK) {
          this.hotKey = res.data.hotkey.slice(0, 10) // 10
        }
      })
    },
    // 
    showConfirm () {
      this.$refs.confirm.show()
    },
    ...mapActions([
      'clearSearchHistory'
    ])
  },
  components: {
    SearchBox,
    Suggest,
    SearchList,
    Confirm,
    Scroll
  },
  watch: {
    // 
    query (newQuery) {
      // 
      if (!newQuery) {
        setTimeout(() => {
          // 
          this.$refs.shortcut.refresh()
        }, 20)
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .search
    .search-box-wrapper
      margin: 20px
    .shortcut-wrapper
      position: fixed
      top: 178px
      bottom: 0
      width: 100%
      .shortcut
        height: 100%
        overflow: hidden
        .hot-key
          margin: 0 20px 20px 20px
          .title
            margin-bottom: 20px
            font-size: $font-size-medium
            color: $color-text-l
          .item
            display: inline-block
            padding: 5px 10px
            margin: 0 20px 10px 0
            border-radius: 6px
            background: $color-highlight-background
            font-size: $font-size-medium
            color: $color-text-d
        .search-history
          position: relative
          margin: 0 20px
          .title
            display: flex
            align-items: center
            height: 40px
            font-size: $font-size-medium
            color: $color-text-l
            .text
              flex: 1
            .clear
              extend-click()
              .icon-clear
                font-size: $font-size-medium
                color: $color-text-d
    .search-result
      position: fixed
      width: 100%
      top: 178px
      bottom: 0
</style>



