<template>
  <div class="singer" ref="singer">
    <!-- @select  -->
    <list-view @select="selectSinger" :data="singers" ref="list"></list-view>
    <!--  -->
    <router-view></router-view>
  </div>
</template>

<script>
// 
import { getSingerList } from '@/api/singer'
// 
import ListView from '@/base/listview/listview'
// 
import Singer from '@/common/js/singer'
import { ERR_OK } from '@/api/config'
// Vuex store
import {mapMutations} from 'vuex'
import {playlistMixin} from '@/common/js/mixin' // 

const HOT_NAME = '';
const HOT_SINGER_NUM = 10; // 10

export default {
  mixins: [playlistMixin],
  data () {
    return {
      singers: []
    }
  },
  created () {
    this._getSingerList();
  },
  methods: {
    // minibottom
    handlePlaylist (playlist) {
      const bottom = playlist.length > 0 ? '60px' : ''
      this.$refs.singer.style.bottom = bottom // minibottom
      this.$refs.list.resetScroll() // listscroll
    },
    // 
    selectSinger (singer) {
      // 
      this.$router.push({
        path: `/singer/${singer.id}`
      })
      // Vuex
      this.setSinger(singer)
    },
    _getSingerList () {
      getSingerList().then((res) => {
        if (res.code === ERR_OK) {
          this.singers = this._normalizeSinger(res.data.list)
        }
      })
    },
    //  
    _normalizeSinger (list) {
      let map = {
        hot: {
          title: HOT_NAME,
          items: []
        }
      };
      // 
      list.forEach((item, index) => {
        if (index < HOT_SINGER_NUM) {
          map.hot.items.push(new Singer({
            id: item.Fsinger_mid,
            name: item.Fsinger_name
          })); // items10
        }
        const key = item.Findex; // 
        // key
        if (!map[key]) {
          // 
          map[key] = {
            title: key,
            items: []
          }
        }
        // key
        map[key].items.push(new Singer({
          id: item.Fsinger_mid,
          name: item.Fsinger_name
        }))
      });
      // map
      let hot = []; // 
      let ret = []; // 
      // map
      for (let key in map) {
        let val = map[key];
        // title
        if (val.title.match(/[a-zA-Z]/)) {
          ret.push(val)
        // 
        } else if (val.title === HOT_NAME) {
          hot.push(val)
        }
      }
      // ret
      ret.sort((a, b) => {
        // charCodeAt Unicode chatAt  
        return a.title.charCodeAt(0) - b.title.charCodeAt(0)
      });
      // 
      return hot.concat(ret)
    },
    // VuexStore  this.setSinger
    ...mapMutations({
      // setSinger mutations-types
      setSinger: 'SET_SINGER'
    })
  },
  components: {
    ListView
  }
}
</script>


<style lang="stylus" scoped>
  .singer
    position: fixed
    top: 88px
    bottom: 0
    width: 100%
</style>




