<template>
  <transition name="slide">
    <music-list :title="title" :bg-image="bgImage" :songs="songs"></music-list>
  </transition>
</template>

<script>
import MusicList from '@/components/music-list/music-list'
import { getDiscSong } from '@/api/recommend' // 
import { ERR_OK } from '@/api/config'
import { mapGetters } from 'vuex' // vuexdisc
import { createSong, getSongUrl } from '@/common/js/song' // 
export default {
  name: 'disc',
  data () {
    return {
      songs: []
    }
  },
  computed: {
    title () {
      return this.disc.title
    },
    bgImage () {
      return this.disc.cover
    },
    ...mapGetters([
      'disc'
    ])
  },
  created () {
    this._getDiscSong()
  },
  methods: {
    _getDiscSong () {
      // id 
      if (!this.disc.content_id) {
        this.$router.push('/recommend')
        return
      }
      getDiscSong(this.disc.content_id).then((res) => {
        if (res.code === ERR_OK) {
          this.songs = this._normalizeSongs(res.cdlist[0].songlist)
        }
      })
    },
    // 
    _normalizeSongs (list) {
      let ret = []
      list.forEach((musicData) => {
        if (musicData.songid && musicData.albumid) {
          ret.push(createSong(musicData))
        }
      })
      // 
      ret.forEach(item => {
        item.url = getSongUrl(item, item.mid)
      })
      return ret
    }
  },
  components: {
    MusicList
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .slide-enter-active, .slide-leave-active
    transition: all 0.3s

  .slide-enter, .slide-leave-to
    transform: translate3d(100%, 0, 0)
</style>



