<template>
  <!--  -->
  <scroll class="suggest"
          :data="result"
          :pullup="pullup"
          :beforeScroll="beforeScroll"
          @beforeScroll="listScroll"
          @scrollToEnd="searchMore"
          ref="suggest"
  >
    <ul class="suggest-list">
      <li @click="selectItem(item)" class="suggest-item" v-for="(item, index) in result" :key="index">
        <div class="icon">
          <i :class="getIconCls(item)"></i>
        </div>
        <div class="name">
          <p class="text" v-html="getDisplayName(item)"></p>
        </div>
      </li>
      <loading v-show="hasMore" title=""></loading>
    </ul>
    <div class="no-result-wrapper" v-show="!result.length">
      <no-result title=""></no-result>
    </div>
  </scroll>
</template>

<script>
import Scroll from '@/base/scroll/scroll' // 
import Loading from '@/base/loading/loading' // 
import Singer from '@/common/js/singer' // 
import NoResult from '@/base/no-result/no-result' // 
import { Search } from '@/api/search' // 
import { ERR_OK } from '@/api/config'
import { createSong, getSongUrl } from '@/common/js/song' // 
import { mapMutations, mapActions } from 'vuex';

const TYPE_SINGER = 'singer' // 
const PERPAGE = 20 // 

export default {
  props: {
    query: {
      type: String,
      default: ''
    },
    showSinger: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      page: 1, // 
      result: [],
      pullup: true, // 
      hasMore: false, // 
      beforeScroll: true // 
    }
  },
  methods: {
    // 
    selectItem (item) {
      // 
      if (item.type === TYPE_SINGER) {
        const singer = new Singer({
          id: item.singermid,
          name: item.singername
        })
        // 
        this.$router.push({
          path: `/search/${singer.id}`
        })
        // vuex
        this.setSinger(singer)
      } else {
        // 
        this.insertSong(item)
      }
      //  
      this.$emit('selectSearch')
    },
    // 
    refresh () {
      this.$refs.suggest.refresh()
    },
    // 
    searchMore () {
      // 
      if (!this.hasMore) {
        return
      }
      this.page++ // +1
      // 
      Search(this.query, this.page, this.showSinger, PERPAGE).then((res) => {
        if (res.code === ERR_OK) {
          //  result
          this.result = this.result.concat(this._getResult(res.data))
          // 
          this._checkMore(res.data)
        }
      })
    },
    // 
    getIconCls (item) {
      // 
      if (item.type === TYPE_SINGER) {
        return 'icon-mine'
      } else {
        return 'icon-music'
      }
    },
    // 
    getDisplayName (item) {
      // 
      if (item.type === TYPE_SINGER) {
        return item.singername
      } else {
        // 
        return `${item.name} - ${item.singer}`
      }
    },
    // 
    _search () {
      this.page = 1 // 
      this.$refs.suggest.scrollTo(0, 0) // 
      this.hasMore = true // 
      Search(this.query, this.page, this.showSinger, PERPAGE).then(res => {
        if (res.code === ERR_OK) {
          this.result = this._getResult(res.data)
          // 
          this._checkMore(res.data)
        }
      })
    },
    // 
    _checkMore (data) {
      const song = data.song
      //   +  * PERPAGE > 
      if (!song.list.length || (song.curnum + song.curpage * PERPAGE) >= song.totalnum) {
        this.hasMore = false
      }
    },
    // 
    _getResult (data) {
      let ret = []
      // 
      if (data.zhida && data.zhida.singerid) {
        ret.push({...data.zhida, ...{type: TYPE_SINGER}})
      }
      // 
      if (data.song) {
        // 
        ret = ret.concat(this._normalizeSong(data.song.list))
      }
      return ret
    },
    // 
    _normalizeSong (list) {
      let ret = []
      list.forEach((musicData) => {
        if (musicData.songmid && musicData.albummid) {
          ret.push(createSong(musicData))
        }
      })
      // 
      ret.forEach(item => {
        ret.url = getSongUrl(item, item.mid)
      })
      // 
      return ret
    },
    // 
    listScroll () {
      this.$emit('listScroll')
    },
    ...mapMutations({
      setSinger: 'SET_SINGER'
    }),
    ...mapActions([
      'insertSong'
    ])
  },
  watch: {
    // 
    query () {
      this._search()
    }
  },
  components: {
    Scroll,
    Loading,
    NoResult
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .suggest
    height: 100%
    overflow: hidden
    .suggest-list
      padding: 0 30px
      .suggest-item
        display: flex
        align-items: center
        padding-bottom: 20px
      .icon
        flex: 0 0 30px
        width: 30px
        [class^="icon-"]
          font-size: 14px
          color: $color-text-d
      .name
        flex: 1
        font-size: $font-size-medium
        color: $color-text-d
        overflow: hidden
        .text
          no-wrap()
    .no-result-wrapper
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>



